import React, { FC, useState } from 'react';
import './styles.scss';
import Container from 'react-bootstrap/Container';
import { LogoLink } from '../UI/logo-link/LogoLink';

import { Sidebar } from '../sidebar/Sidebar';
import { useDisclosure } from '../../hooks/useDisclosure';
import SvgIconMenu from '../icons/Menu';

import StickyBox from 'react-sticky-box';
// import { SidebarOffCanvas } from '../sidebar/SidebarOffCanvas';
import Button from 'react-bootstrap/Button';
import Offcanvas from 'react-bootstrap/Offcanvas';
import { Nav } from '../nav/Nav';

const PageHeader: FC = () => {
  const { open, close, isOpen } = useDisclosure(false);

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <StickyBox style={{ zIndex: 5 }}>
        <header className='page-header'>
          <Container>
            <div className='page-header__label'>Рядом с домом</div>
            <LogoLink />

            <button
              type='button'
              className='page-header__btn-menu'
              aria-label='Открыть меню'
              // onClick={open}
              onClick={handleShow}
            >
              <SvgIconMenu />
            </button>
          </Container>
        </header>
      </StickyBox>
      {/* <Sidebar isOpen={isOpen} close={close} /> */}

      <Offcanvas show={show} onHide={handleClose} placement={'end'}>
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>
            <LogoLink />
            <small>рядом с домом</small>
          </Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <Nav close={handleClose} />
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
};

export { PageHeader };
